<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-13",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            `,
            css: `
            `
        };
    },
    mounted() {
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        ctx.strokeStyle = 'darkseagreen';
        ctx.fillStyle = 'green';
        
        let stepx = 0;
        let stepy = 0;
        let stepAng = 0;
        let stepSca = 0;
        let tmpx = 1;
        let tmpy = 1;
        let tmpSca = 0.1;
        let x = 0;
        let y = 0;
        let sx = 0;
        let sy = 0;
        window.requestAnimationFrame(draw);
        function draw() {
            // 移动
            x = 100+stepx;
            y = 100+stepy;
            if(x>700) {
                tmpx = -1;
            }else if( x < 100) {
                tmpx = 1;
            }
            if(y>400) {
                tmpy = -1;
            }else if( y < 100) {
                tmpy = 1;
            }
            stepx+=tmpx;
            stepy+=tmpy;
            // 旋转
            stepAng += 10;
            // 缩放
            sx = stepSca;
            sy = 2-stepSca;
            if(stepSca > 2) {
                tmpSca = -0.1;
            }else if (stepSca < 0) {
                tmpSca = 0.1;
            }
            stepSca += tmpSca;

            ctx.save();
            ctx.clearRect(0, 0, canvas.width, canvas.height);

            ctx.translate(x, y);
            ctx.rotate((Math.PI / 180) * stepAng);
            ctx.scale(sx, sy);
            ctx.beginPath();
            ctx.arc(0, 0, 30, 0, (Math.PI/180)*360, false);
            ctx.fill();

            ctx.restore();
            window.requestAnimationFrame(draw);
        }
        

        // 渲染图片
        // ctx.save();
        // ctx.translate(50, 30);
        // ctx.arc(0, 0, 20, 0, Math.PI*2, true);
        // ctx.clip();
        
        // // ctx.restore();
        // // ctx.save();
        // // ctx.translate(10, 10);
        // ctx.fillRect(0, 0, 50, 50);
        // // ctx.restore();
        // // ctx.save();
        // // ctx.translate(100, 10);
        // // ctx.rotate((Math.PI/180)*60);
        // // ctx.scale(0.5, 0.8);
        // // ctx.fillRect(0, 0, 50, 50);
        
        
    }
};
</script>

